import { Component, OnInit, AfterViewInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { MainService } from './../../main.service';
import { FormGroup, FormBuilder, FormArray, FormControl } from '@angular/forms';

@Component({
  selector: 'app-dsr-report-popup',
  templateUrl: './dsr-report-popup.component.html',
  styleUrls: ['./dsr-report-popup.component.css']
})
export class DsrReportPopupComponent implements OnInit, AfterViewInit  {
  allPos: any = [];
  pos: any;
  empData: any;
  loading: boolean = false;
  mappedPos: any =  {
    id: []
  };
 
   myForm: FormGroup;

  constructor(
    private mainService: MainService,
    public dialogRef: MatDialogRef<DsrReportPopupComponent>,
    private fb: FormBuilder
  ) {
    this.pos = JSON.parse(localStorage.getItem('pos'));
    this.mappedPos = JSON.parse(localStorage.getItem('mappedPos'));
    this.empData = JSON.parse(localStorage.getItem('employee'));
   }

  ngOnInit() {
   
    this.myForm = this.fb.group({
      mappedPos: this.fb.array([])
    });
  }

  ngAfterViewInit() {
    this.allPos.push(this.pos);
    if (this.mappedPos && this.mappedPos.length > 0) {
      this.mappedPos.forEach(element => {
          this.allPos.push(element.posDetails);
      });
    }
    for (let index = 0; index < this.allPos.length; index++) {
      this.allPos[index].is_Checked =  true;
    }
    this.loading = false;
  }

  onClose(status) {
    this.dialogRef.close(status);
  }

  GenerateDSRReport() {
    var chkd = [];
    for (let index = 0; index < this.allPos.length; index++) {
        if (this.allPos[index].is_Checked) {
          chkd.push(this.allPos[index].id);
        }
    }
    this.loading = false;
    this.onClose(chkd);
  }


}
