import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MainService } from '../../main.service';
import { SnackBarService } from 'src/app/services/snackbar.service';
import { Location } from '@angular/common';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import * as CryptoJS from 'crypto-js';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-rest-qrcode',
  templateUrl: './rest-qrcode.component.html',
  styleUrls: ['./rest-qrcode.component.css']
})
export class RestQrcodeComponent implements OnInit {

  pos: any;
  mappedPos: any;
  selectedPosId: any;
  public UserQRCodeData: string = null;
  href: string;
  outside_rest_type: any;
  bhojOrderPos: any = [];
  out_rest_no: any;
  isQrCodeShow: boolean = false;

  constructor(
    private router: Router,
    private snackbar: SnackBarService,
    private mainService: MainService,
    private location: Location,
    private httpClient: HttpClient
    ) {
      this.UserQRCodeData = '';
      this.pos = JSON.parse(localStorage.getItem('pos'));
      this.mappedPos = JSON.parse(localStorage.getItem('mappedPos'));
    }

  ngOnInit() {
    const restcode = JSON.parse(localStorage.getItem('loginData')).restaurentCode;
    console.log(this.pos);
    if ( this.pos.posTypeName === 'BHOJ_ORDER') {
      this.bhojOrderPos.push(this.pos);
    }
    if (this.mappedPos && this.mappedPos.length > 0) {
        this.mappedPos.forEach(element => {
          console.log(element);
          if ( element.posDetails.posTypeName === 'BHOJ_ORDER') {
            this.bhojOrderPos.push(element.posDetails);
          }
        });
    }
  }


  downloadqr() {
    console.log(document);
    this.href = document.getElementsByTagName('img')[1].src;
  }

  back() {
    this.location.back();
  }

  TypeNone() {
    const posId = ( this.selectedPosId) ? this.selectedPosId.id : this.pos.id;
    this.mainService.getQrCodeData(posId, 'none', 0 ).subscribe(data => {
    if (data.status === 'success') {
      this.isQrCodeShow = true;
      this.UserQRCodeData = data.response.outsideRestUserQRCode;
    }
    });
  }

  RemoveQrCode() {
    this.UserQRCodeData = '';
  }
  generateQrCode() {
      const posId = ( this.selectedPosId) ? this.selectedPosId.id : this.pos.id;
      this.mainService.getQrCodeData(posId, this.outside_rest_type, this.out_rest_no ).subscribe(data => {
      if (data.status === 'success') {
        this.isQrCodeShow = true;
        this.UserQRCodeData = data.response.outsideRestUserQRCode;
      }
      });
  }
}
