import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { MainComponent } from './components/main/main.component';
import { HeaderComponent } from './components/main/header/header.component';
import { SidebarComponent } from './components/main/sidebar/sidebar.component';
import { AuthGuard } from './misc/auth.guard';
import { DashboardComponent } from './components/main/dashboard/dashboard.component';

import { ItemsResolver } from './resolvers/items.resolver';
import { AllItemsResolver } from './resolvers/all-items.resolver';
import { CategoryResolver } from './resolvers/category.resolver';
import { SubcategoryResolver } from './resolvers/subcategory.resolver';
import { TaxDetailsResolver } from './resolvers/tax-details.resolver';
import { AddonResolver } from './resolvers/addons.resolver';
import { RemarkResolver } from './resolvers/remarks.resolver';
import { RestcategoryResolver } from './resolvers/rest-category.resolver';
import { HsnMasterResolver } from './resolvers/hsn-master.resolver';
import { DiningTableResolver } from './resolvers/dining-table.resolver';
import { TableOrderResolver } from './resolvers/table-order.resolver';
import { CustomersResolver } from './resolvers/customers.resolver';
import { ParcelOrderResolver } from './resolvers/parcel-order.resolver';
import { TokenOrderResolver } from './resolvers/token-order.resolver';
import { MapperPrinterResolver } from './resolvers/mapped-printers.resolver';
import { PrinterResolver } from './resolvers/printer.resolver';
import { KitchenResolver } from './resolvers/kitchen.resolver';
import { PosResolver } from './resolvers/pos.resolver';
import { ZomatoComponent } from './components/main/zomato/zomato.component';
import { MobileOrderComponent } from './components/main/mobile-order/mobile-order.component';
import { ZomatoOrderResolver } from './resolvers/zomato-order.resolver';
import { MobileOrderResolver } from './resolvers/mobile-order.resolver';
import { ParcelService } from './components/main/parcel/parcel.service';
import { SettingComponent } from './components/main/setting/setting.component';
import { RestQrcodeComponent } from './components/main/setting/rest-qrcode/rest-qrcode.component';
import { BillsComponent } from './components/main/bills/bills.component';
import { DsrReportComponent } from './components/main/dsr-report/dsr-report.component';
import { SwiggyComponent } from './components/main/swiggy/swiggy.component';
import { OrderHistoryComponent } from './components/main/order-history/order-history.component'

const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  {
      path: 'app', component: MainComponent,
      resolve: {    },
      children: [
        { path: '', component: DashboardComponent },
        {
            path: 'dashboard', component: DashboardComponent
        },
        {
            path: 'settings', component: SettingComponent
        },
        {
            path: 'bills', component: BillsComponent
        },
        {
            path: 'dsr-report', component: DsrReportComponent
        },
        {
            path: 'rest-qrcode', component: RestQrcodeComponent
        },
        {
            path: 'order-history', component: OrderHistoryComponent
        },
        {
            path: 'order/:posId', component: MobileOrderComponent, resolve: {
                orders: MobileOrderResolver
            }
       },
       {
           path: 'zomato/:posId', component: ZomatoComponent, resolve: {
               orders: ZomatoOrderResolver
           }
       },
       {
           path: 'swiggy/:posId', component: SwiggyComponent, resolve: {
               orders: ZomatoOrderResolver
           }
       }
      ]
  },
  // { path: '**', component: LoginComponent }
];

@NgModule({
  declarations: [],
  imports: [
     RouterModule.forRoot(routes, { enableTracing: false })
    ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
