import { Component, OnInit, AfterViewInit } from '@angular/core';
import { LoginService } from '../../login/login.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SnackBarService } from '../../../services/snackbar.service';
import { MainService } from '../main.service';
import { CookieService } from 'ngx-cookie-service';
import { NgxSpinnerService } from 'ngx-spinner';
import { timer } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { MatDialogRef, MAT_DIALOG_DATA, MatOption, MatSelect, MatAutocompleteSelectedEvent} from '@angular/material';
import { MatDialog } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';
declare var $: any;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, AfterViewInit {
  subscription: any;
  sub: Subscription;
  orderTypeId: any;

  localStoreGetData;
  macAdd;
  zomatoPos: any;
  mobileOrdrerPos: any;
  activeLink;
  isLoading: boolean;
  isParcelPos: any;
  isShopToken: any;
  serverlog;
  btncolor: any;
  posData: any;
  mappedPosData: any;
  enableBtn = {
      dining: false,
      parcel: false,
      mobile: false,
      zomato: false,
      token: false,
      shopToken: false
   };
   loading: boolean = false;
   enableBtnMpos = false;
   orderTypesArry = [];
   currUrlId;
  mappedPosArray: any[];
  constructor(
    private loginService: LoginService,
    private router: Router,
    private snackBar: SnackBarService,
    private mainService: MainService,
    private cookieService: CookieService,
    private spinner: NgxSpinnerService,
    private activeRoute: ActivatedRoute,
    private httpClient: HttpClient,
    private dialog: MatDialog
  ) {
    this.spinner.hide();
    router.events.subscribe((val) => {
    setTimeout(() => {
        this.activeLink = mainService.activeLink;
      }, 1000);
    });

}

  ngOnInit() {
    this.btncolor = '#006400';
    this.activeLink = this.mainService.activeLink;
    this.checkEnabledButton();
  this.loading = false;    
    timer(0, 10000).subscribe(val => {
    const posinfo = JSON.parse(localStorage.getItem('pos'));
   // console.log(posinfo);
    if (posinfo) {
      this.mainService.getMappedPosData().subscribe((data: any) => {
          this.mappedPosArray = [];
          var resdata = data.response;
          if (resdata.length > 0 ) {
            for (let index = 0; index < resdata.length; index++) {
              const arr = {
                posId : resdata[index].posId,
                totalPosOrder : (resdata[index].ordersList) ? resdata[index].ordersList.length : 0
              };
              this.mappedPosArray.push(arr);
              
            }
            //console.log('ress', this.mappedPosArray);
          }
      });
    }
    });

    const dataChange =  this.mainService.cancelOrderObservable$.subscribe(posId => {
     return this.mappedPosArray.filter(data => {
        if (posId === data.posId) {
          if(data.totalPosOrder == 1){
            data.totalPosOrder = 0
          }
        }  
      });
       setTimeout( () => {  dataChange.unsubscribe(); }, 400);
    });
  }

  ngAfterViewInit(): void {
    setInterval(() => {
       this.currUrlId = location.href.split('/')[6];
      }, 500);
   }

  logout() {
    this.router.navigate(['/login']);
    this.snackBar.showSuccess('Logout Successfully');
    setTimeout(() => {
      localStorage.removeItem('authKey');
      localStorage.removeItem('pos');
      localStorage.removeItem('mappedPos');
      localStorage.removeItem('loginData');
      localStorage.removeItem('listItem');
      localStorage.removeItem('employee');
      localStorage.removeItem('token');
      localStorage.removeItem('macAddress')
    },500);
    
    // this.loginService.logout().subscribe(res => {
    //   this.router.navigate(['/login']);
    //   this.snackBar.showSuccess('Logout Successfully');
    // });
  }

  serverSessionLogout() {
    this.loading = true;
    this.macAdd = localStorage.getItem('macAddress');
    this.localStoreGetData = JSON.parse(localStorage.getItem('loginData'));
    if (this.localStoreGetData) {
      const userDt = {
        email: this.localStoreGetData.email,
        restCode: this.localStoreGetData.restCode,
        password: this.localStoreGetData.password,
        macAddress: this.macAdd
      };
      this.mainService.serverLogout(userDt)
      .subscribe(res => {
        if(res.status === 'success') {
          this.logout();
        } else {
          (res.response) ? this.snackBar.showError(res.response): this.snackBar.showError('Something went wrong. Please try again...') ;
        }
        this.loading = false;
      });
    }
    setTimeout( () => {
      this.loading = false;
    }, 25000);
  }

  checkEnabledButton() {

    this.posData = JSON.parse(localStorage.getItem('pos'));
    this.mappedPosData = JSON.parse(localStorage.getItem('mappedPos'));

    if ( this.posData && this.posData.posTypeName == 'ZOMATO' ) {
      this.enableBtn.zomato = true;
  }  else if ( this.posData && this.posData.posTypeName == 'BHOJ_ORDER' ) {
      this.enableBtn.mobile = true;
  } 

    if (this.mappedPosData && this.mappedPosData.length > 0) {
        this.mappedPosData.forEach((element) => {
            if (element.posDetails.posTypeName === 'ZOMATO') {
              this.enableBtn.zomato = true;
              this.enableBtnMpos = true;
            } else if (element.posDetails.posTypeName === 'BHOJ_ORDER') {
              this.enableBtn.mobile = true;
            }
      });
    }

  }
  
  
    // this.zomatoPos = this.posData.posTypeId == '1a7e52fe-5122-11e9-9996-bc5ff4c06e58' ? { ...this.posData } : false;
    // if (!this.zomatoPos) {
    //     this.zomatoPos = this.mappedPosData.find(m => m.posTypeId == '1a7e52fe-5122-11e9-9996-bc5ff4c06e58');
    // }

    // this.mobileOrdrerPos = this.posData.posTypeId == '405a7604658711e996d7bc5ff4c06e58' ? { ...this.posData } : false;
    // if (!this.mobileOrdrerPos) {
    //     this.mobileOrdrerPos = this.mappedPosData.find(m => m.posTypeId == '405a7604658711e996d7bc5ff4c06e58');
    // }

  // }


}