import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { environment } from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { of } from 'rxjs';
import { MainService } from '../main.service';


@Injectable()
export class OrderService {
    constructor(private httpClient: HttpClient, private mainService: MainService) {

    }

    getNextKotNo(posId): Observable<any> {
        posId = this.mainService.pos.id;
        return this.httpClient.get(`${environment.localApi}/pos/getKotNo/${posId}`)
            .map((res: any) => {
                //return res.response.kotNo;
                return res;
            });
    }

    getShopTokenNextKotNo(posId): Observable<any> {
        posId = this.mainService.ShopToken.id;
        return this.httpClient.get(`${environment.localApi}/pos/getKotNo/${posId}`)
            .map((res: any) => {
                return res.response.kotNo;
            });
    }

    getTableOrder(tableId): Observable<any> {
        return this.httpClient.get(environment.localApi + `/order/table/${tableId}`)
            .map((res: any) => {
                return res.response;
            });
    }
}
