import { Component, OnInit } from '@angular/core';
import { SnackBarService } from '../../services/snackbar.service';
import { FormGroup, FormControl } from '@angular/forms';
import { LoginService } from './login.service';
import { MainService } from './../main/main.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { CookieService } from 'ngx-cookie-service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NgxLoadingModule, ngxLoadingAnimationTypes } from 'ngx-loading';
import { environment } from '../../../environments/environment';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  isLoading: boolean;
  loading: boolean = false;
  timerId: any;
  constructor(
    private loginService: LoginService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private main:  MainService,
    private cookieService: CookieService,
    private httpClient: HttpClient,
    private snackBar: SnackBarService
  ) { }

    loginForm: FormGroup;
    cookieEmail: string;
    cookiePassword: any;
    cookierestaurentCode: any;
    isChecked: boolean = false;
    count10: number = 1;
    macAddress: any;
    ngOnInit() {
        this.createForm();
        const options = {
          headers: new HttpHeaders({
              'Content-Type': 'application/json',
          })
       };
        this.httpClient.get( environment.phpUrl + 'qr-code.php', options).subscribe((data: any) => {
          console.log(data);
          this.macAddress = data.response;
        });
    }

  createForm() {
    this.cookieEmail = this.cookieService.get('email');
    this.cookiePassword = this.cookieService.get('password');
    this.cookierestaurentCode = this.cookieService.get('restaurentCode');
    if (this.cookieEmail !== '') {
      this.loginForm = new FormGroup({
        email: new FormControl(this.cookieEmail),
        password: new FormControl(this.cookiePassword),
        restaurentCode: new FormControl(this.cookierestaurentCode),
        rememberMe: new FormControl(true),
        isfrp_set: new FormControl(true)
      });
      this.onChange(true);
    } else {
      this.loginForm = new FormGroup({
        email: new FormControl(''),
        password: new FormControl(''),
        restaurentCode: new FormControl(''),
        rememberMe: new FormControl(false),
        isfrp_set: new FormControl(true) 
      });
      this.onChange(false);
    }

  }

  onChange(e) {
    this.isChecked = e;
    localStorage.setItem('rememberMe', JSON.stringify(this.isChecked));
  }

  onSubmit() {
    this.loading = true;
    this.loginForm.value.email = this.loginForm.value.email.toLowerCase();
    const data = {
        email: this.loginForm.value.email,
        restCode: this.loginForm.value.restaurentCode,
        password: this.loginForm.value.password,
        macAddress: this.macAddress,
        bhoj_online_order: true
       };
    
    this.loginService.login(data).subscribe(res => {
      this.loading = false;
      if (res && res.status === 'success') {
        this.router.navigate([`app/order/${res.response.pos.posDetails.id}`]);
      } else if (res && res.status === 'error') {
        if (res.response.loginCredentialValid) {
          this.snackBar.showError(res.response.loginCredentialValid);
          return;
        } 
        if (res.response.password) {
          this.snackBar.showError(res.response.password);
          return;
        } 
        if (res.response.restCode) {
          this.snackBar.showError(res.response.restCode);
          return;
        }  
        (res.response) ? this.snackBar.showError(res.response) : null ;
      } else {
        (res.response.loginCredentialValid) ? this.snackBar.showError(res.response.loginCredentialValid) : null ;
      }
    });
  }
}
