import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'tableFilterPipe' })
export class TableFilterPipe implements PipeTransform {
    transform(data, field, value, searchText) {
        if (searchText) {
            data = data.filter(t => t.tableCode.toString().includes(searchText));
        }

        if (value == 'ALL') {
            return data;
        }
        const result = [];
        for (const val of data) {
            if (val[field] == value) {
                result.push(val);
            }
        }
        return result;
    }
}
