import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment as env } from '../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { of } from 'rxjs';
import { CookieService } from 'ngx-cookie-service';
import { NgxSpinnerService } from 'ngx-spinner';
import { SnackBarService } from '../../services/snackbar.service';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
        

  constructor(
    private httpClient: HttpClient,
    private cookieService: CookieService,
    private spinner: NgxSpinnerService,
    private snackBar: SnackBarService
  ) { }

  login(data: any) {
    const options = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json',
        })
    };
    return this.httpClient.post(env.serverApi + 'lite/employee/login', data, options)
    .map((res: any) => {
        if (res.status === 'success' && res.response) {
        localStorage.setItem('employee', JSON.stringify(res.response.loginEmployee));
        localStorage.setItem('pos', JSON.stringify(res.response.pos.posDetails));
        localStorage.setItem('mappedPos', JSON.stringify(res.response.mappedPos));
        localStorage.setItem('authKey', res.response.loginEmployee.authkey);
        localStorage.setItem('loginTime', new Date().getTime().toString());
        localStorage.setItem('loginData', JSON.stringify(data));
        localStorage.setItem('macAddress', res.response.loginEmployee.macAddress);
        if (localStorage.getItem('rememberMe') === 'true') {
            this.cookieService.set('email', data.email);
            this.cookieService.set('password', data.password);
            this.cookieService.set('restaurentCode', data.restCode);
          } else {
              this.cookieService.deleteAll();
          }
        }
        return res;
    });
}

logout(): Observable<any> {
    return this.httpClient.post(env.serverApi + 'lite/employee/logout', {})
    .map(res => {
        localStorage.removeItem('authKey');
        localStorage.removeItem('pos');
        localStorage.removeItem('mappedPos');
        localStorage.removeItem('loginData');
        localStorage.removeItem('listItem');
        localStorage.removeItem('employee');
        localStorage.removeItem('macAddress');
        return res;
    });
}
}
