import { Component, OnInit, AfterViewInit } from '@angular/core';
import { MainService } from '../main.service';
import { NgxSpinnerService } from 'ngx-spinner';
import {MatCardModule} from '@angular/material/card';
import { SnackBarService } from '../../../services/snackbar.service';
import { DatePipe } from '@angular/common';
// import { PosexpireDialogComponent } from '../dialogs/posexpire-dialog/posexpire-dialog.component';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-order-history',
  templateUrl: './order-history.component.html',
  styleUrls: ['./order-history.component.css']
})
export class OrderHistoryComponent implements OnInit {
  loading = true;
  pos: any;
  posData;
  mappedPosData;
  posresult;
  data;
  activeElement;
  salesdays: number = 0;
  reportType;
  pos_id;
  ondaychange;
  posId:any;
  activeBtn;
  pageno:any;
  total_pages;
  allPages: any = [];
  allorders;
  datefrom;
  dateto;
  datePipe = new DatePipe('en-IN');
  minDate=new Date(new Date().setDate(new Date().getDate() - 30));
  maxDate = new Date();
  constructor(
    private mainService: MainService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private snackBar: SnackBarService
    ) {}

  ngOnInit() {
   this.getALLorderhistory();
   this.ondaychange = 'Today';
  }
   

  getALLorderhistory(){
    this.allPages = [];
    this.posData = JSON.parse(localStorage.getItem('pos'));
    this.pos = JSON.parse(localStorage.getItem('pos'));
    this.salesdays = 0;
    const arr=[];
    this.pageno = 1;
    this.mappedPosData = JSON.parse(localStorage.getItem('mappedPos'));
    arr.push(this.pos.id);
        // 0 = Today,1 = Yesterday, 2 = This Week, 3 = Last Week,4 = This month,5 = Last month
        let data = { 'saleDayType':0 ,"startDate":"",
        "endDdate":"",
        "posIdList":arr
    };
    this.posresult =this.mappedPosData;
     this.mainService.order_history(data,this.pageno)
         .subscribe(res => {
          console.log(res);
           if(res.status== "success"){
            this.data = res.response.listOfOrderHistory;
            this.total_pages = res.response.totalPage;
            for (let i = 1; i <= this.total_pages; i++) {
              this.allPages.push(i);
          }
             this.posresult =this.mappedPosData;
           }else{
             return false;
           }     
        this.loading = false;
       });
  }
   
  selectday(n){
   
    this.datefrom = null;
    this.dateto = null;
    this.salesdays = n;
    this.pageno = 1
    this.orderhistory_data(false);
  }

  orderhistory_data( isSubmit = false){
    this.loading = true;
    this.pageno = 1;
    var datefromFormat = '';
    var datetoFormat = '';
    if (isSubmit) {
      if(this.datefrom == null) {
        this.snackBar.showError('Please Select Start Date');
        return;
      } if(this.dateto == null){
        this.snackBar.showError('Please Select End Date');
        return;
      }
        let datefrom = (this.datefrom._d)?this.datefrom._d:'';
         datefromFormat = this.datePipe.transform(datefrom, 'yyyy-MM-dd HH:mm:ss');
        let dateto = (this.dateto._d)?this.dateto._d:'';
         datetoFormat = this.datePipe.transform(dateto, 'yyyy-MM-dd 23:59:59');
          if (datefrom > dateto) {
            this.snackBar.showAlert('Start Date can not be bigger than End Date');
            return;
          }
        }
        const arr=[];
        arr.push(this.pos.id);
        const data = { 'saleDayType': this.salesdays ,"startDate": datefromFormat ,
            "endDdate":datetoFormat,
            "posIdList":arr
        };
        this.getChangedData(data);
    }

    getChangedData(reqData) {
      this.allPages = [];
      this.mainService.order_history(reqData, this.pageno)
      .subscribe(res => {
       console.log(res);
       this.loading = false;
        if(res.status== "success"){
         this.data = res.response.listOfOrderHistory;
         this.total_pages = res.response.totalPage;
         for (let i = 1; i <= this.total_pages; i++) {
           this.allPages.push(i);
       }
          this.posresult =this.mappedPosData;
        }else{
          return false;
        }     
     this.loading = false;
    });
    }
    
    getAllBillsByPageNo(pageno) {
      this.loading = true;
      this.allPages = [];
      this.pageno = pageno;
      console.log(this.pos);
    if(this.datefrom == null || this.dateto == null){
      console.log('love');
      let datefrom = '';
      let datefromFormat = this.datePipe.transform(datefrom, 'yyyy-MM-dd HH:mm:ss');
      let dateto = '';
      let datetoFormat = this.datePipe.transform(dateto, 'yyyy-MM-dd 23:59:59');
      const arr=[];
      arr.push(this.pos.id);
          // 0 = Today,1 = Yesterday, 2 = This Week, 3 = Last Week,4 = This month,5 = Last month
          let data = { 'saleDayType': this.salesdays ,"startDate":datefromFormat ,
          "endDdate":datetoFormat,
          "posIdList":arr
      };
      this.mainService.order_history(data,this.pageno).subscribe(res => {
          this.data =res.response.listOfOrderHistory;
          this.total_pages = res.response.totalPage;
          for (let i = 1; i <= this.total_pages; i++) {
              this.allPages.push(i);
          }
          this.loading = false;
          });
      } else {
        console.log('kush');
        let datefrom = (this.datefrom._d)?this.datefrom._d:'';
        let datefromFormat = this.datePipe.transform(datefrom, 'yyyy-MM-dd HH:mm:ss');
        let dateto = (this.dateto._d)?this.dateto._d:'';
        let datetoFormat = this.datePipe.transform(dateto, 'yyyy-MM-dd 23:59:59');
        const arr=[];
      arr.push(this.pos_id);
        // 0 = Today,1 = Yesterday, 2 = This Week, 3 = Last Week,4 = This month,5 = Last month
        let data = { 
          'saleDayType': this.salesdays,
          "startDate":datefromFormat,
          "endDdate":datetoFormat,
          "posIdList":arr
        };
    this.mainService.order_history(data,this.pageno).subscribe(res => {
        this.data =res.response.listOfOrderHistory;
        this.total_pages = res.response.totalPage;
        for (let i = 1; i <= this.total_pages; i++) {
            this.allPages.push(i);
        }
        this.loading = false;
        });
    }
  }

  resetFilter() {
    this.datefrom = null;
    this.dateto = null;
    this.getALLorderhistory();
  }
}
