import { HttpInterceptor, HttpHandler, HttpRequest, HttpEvent, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { SnackBarService } from '../services/snackbar.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { environment } from 'src/environments/environment';

@Injectable()
export class HttpErrorInterceptor implements HttpInterceptor {
    constructor(public snackBarService: SnackBarService, private spinner: NgxSpinnerService) { }
    intercept(
        req: HttpRequest<any>,
        next: HttpHandler
    ): Observable<HttpEvent<any>> {
        return next.handle(req).pipe(
            tap(evt => {
                if (evt instanceof HttpResponse) {
                    if (req.url.indexOf('/v2/order') == -1 &&  req.url.indexOf('/printer/print-failed') == -1) {
                        //this.spinner.hide();
                    }
                    if (evt.body && evt.body.status == 'error') {
                       // (evt.body.message) ?  this.snackBarService.showError(evt.body.message) : null;
                    }

                }
            }),
            catchError((err: any) => {
                if (err instanceof HttpErrorResponse) {
                    // console.log(err)
                    this.spinner.hide();
                    if(err.error.message){
                        // this.snackBarService.showError(err.error.message);
                        (err.error.message) ?  this.snackBarService.showError(err.error.message) : null;
                    }
                    // this.snackBarService.showError(err.error.message ? err.error.message : 'Something went wrong');
                }
                return of(err);
            }));

    }
}
