import { Injectable } from '@angular/core';
import { MainService } from '.././components/main/main.service';

import { Resolve } from '@angular/router';

import { ActivatedRouteSnapshot } from '@angular/router';
import { HelperService } from '../misc/helper.service';

@Injectable()
export class RestcategoryResolver implements Resolve<any> {
  constructor(private mainService: MainService, private helperService: HelperService) {}

  resolve(route: ActivatedRouteSnapshot) {
    return this.mainService.getRestCategory();
  }
}
