import { Component, OnInit, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { MainService } from '../main.service';
import { ActivatedRoute, Router } from '@angular/router';
import { HelperService } from '../../../misc/helper.service';
import { SnackBarService } from '../../../services/snackbar.service';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';
import { MatDialog } from '@angular/material';
import { NgxSpinnerService } from 'ngx-spinner';
import { DatePipe } from '@angular/common';
import * as _moment from 'moment';
import { from } from 'rxjs';
import { BillKotDetailsDialogComponent } from '../../main/dialogs/bill-kot-details-dialog/bill-kot-details-dialog.component';
import { BillItemDetailsDialogComponent } from '../../main/dialogs/bill-item-details-dialog/bill-item-details-dialog.component';

export const CUSTOM_DATE_FORMAT  = {
  parse: {
    dateInput: 'DD/MM/YYYY',
    // dateInput: 'DD/MM/YYYY HH:mm:ss',
  },
  display: {
    dateInput: 'DD/MM/YYYY',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'DD/MM/YYYY',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};

@Component({
  selector: 'app-bills',
  templateUrl: './bills.component.html',
  styleUrls: ['./bills.component.css'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'en-IN'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: CUSTOM_DATE_FORMAT},
  ],
})
export class BillsComponent implements OnInit, AfterViewInit{
  loading = true;
  allBills = [];
  pageno: any;
  selectedAll: any;
  searchText;
  noDSR: boolean = false;
  daterow: boolean = false;
  datePipe = new DatePipe('en-IN');
  maxDate = new Date();
  total_pages: any;
  allPages: any = [];
  datefrom ;
  dateto ;
  dynamicClass = 'alert alert-danger';
  dataNotFound: any = 'Bill not found.......';
  dataNotFoundCond: boolean = false;
  datefilterdata: any = [];
  constructor(
    private mainService: MainService,
    private activatedRoute: ActivatedRoute,
    private snackBar: SnackBarService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.pageno = 1;
    this.mainService.allBillList(this.pageno, '', '').subscribe(res => {
      this.loading = false;
      console.log(res);
      this.allBills = res.billBeanList;
    });
  }

ngAfterViewInit(): void {
    this.mainService.allBillList(this.pageno, '', '').subscribe(res => {
      this.total_pages = Math.ceil(res.billNumberCount / 10);
      for (let i = 1; i <= res.pageNo; i++) {
          this.allPages.push(i);
      }
      });
}

getAllBillsByPageNo(pageno) {
  this.loading = true;
  this.allPages = [];
  this.mainService.allBillList(pageno, '', '').subscribe(res => {
    this.allBills = res.billBeanList;
    this.pageno = pageno;
    this.total_pages = Math.ceil(res.billNumberCount / 10);
    for (let i = 1; i <= res.pageNo; i++) {
        this.allPages.push(i);
    }
    this.loading = false;
    });
}

searchData(searchText) {
    this.loading = true;
    if (searchText === '' || searchText === undefined || searchText === null) {
      this.loading = false;
      this.snackBar.showError('Please enter bill number');
      return;
    }
    this.pageno = 1;
    this.dataNotFoundCond = false;
    searchText = searchText.toUpperCase();
    this.mainService.searchBillList(this.pageno, searchText).subscribe(res => {
      this.loading = false;
      if (res.length === 0) {
        this.dataNotFound = 'Bill not found.......';
        this.dataNotFoundCond = true;
        this.dynamicClass = 'alert alert-danger';
        this.allBills = [];
        this.allPages = [];
      } else {
        this.allBills = res;
        this.allPages = [];
      }
    });
}

  submitdate(p = '') {
    this.loading = true;
    if ((this.datefrom === null || this.dateto === null) || (this.datefrom === undefined || this.dateto === undefined) ||
       (this.datefrom === '' || this.dateto === '')) {
        this.snackBar.showError('Please select From date and To date correct');
        this.loading = false;
        return;
    } else {
      // this.searchText = '';
      let datefrom = this.datefrom._d;
      let datefromFormat = this.datePipe.transform(datefrom, 'yyyy-MM-dd HH:mm:ss');
      let dateto = this.dateto._d;
      let datetoFormat = this.datePipe.transform(dateto, 'yyyy-MM-dd 23:59:59');
      this.filterDateWise(p, datefromFormat, datetoFormat);

    }
  }

  filterDateWise(p, fromdate, to) {

    this.daterow = true;
    this.allPages = [];
    const page = (p) ? p : 1;
    this.pageno = page;
    this.datefilterdata = [];
    this.datefilterdata.push(this.pageno);
    this.datefilterdata.push(fromdate);
    this.datefilterdata.push(to);
    this.mainService.allBillList(page, fromdate, to).subscribe(res => {
      console.log(res);
        this.allBills = res.billBeanList;
        this.total_pages = Math.ceil(res.billNumberCount / 10);
        this.loading = false;
        for (let i = 1; i <= res.pageNo; i++) {
            this.allPages.push(i);
        }
    });
}

reset() {
      this.datefrom = null;
      this.dateto = null;
      this.searchText = '';
      this.getAllBillsByPageNo(1);
    }

onBillKot(kotNo, orderId) {
      const testDialogRef = this.dialog.open(BillKotDetailsDialogComponent, {
        width: '800px',
        height: 'auto',
        data: {
          kotNo: kotNo,
          orderId: orderId
        }
      });
      testDialogRef.afterClosed().subscribe(result => {
        if (result) {
        }
      });
    }

  onBillItem(billData) {
      const testDialogRef = this.dialog.open(BillItemDetailsDialogComponent, {
        width: '800px',
        height: 'auto',
        data: {
          billData: billData
        }
      });
      testDialogRef.afterClosed().subscribe(result => {
        if (result) {
        }
      });
    }

    dateChangeDetect() {

      let datefrom =  this.datefrom ? this.datefrom._d : null;
      let from_date = this.datePipe.transform(datefrom, 'yyyy-MM-dd');
      let dateto = this.dateto ? this.dateto._d : null;
      let to_date = this.datePipe.transform(dateto, 'yyyy-MM-dd');
      if (datefrom !== null && dateto !== null) {
        if (from_date > to_date) {
          this.dateto = '';
          this.snackBar.showAlert('From date can not be bigger than To date')
          console.log('from_date date is bigger');
        } else {
          
          console.log('to date is bigger');
        }
      }
      
    }

}
