import { Component, OnInit } from '@angular/core';
import { MainService } from '../main.service';
import { SnackBarService } from '../../../services/snackbar.service';
import { DsrReportPopupComponent } from '../dialogs/dsr-report-popup/dsr-report-popup.component';
import { MatDialog, MatOption, MatSelect } from '@angular/material';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $: any;


@Component({
  selector: 'app-dsr-report',
  templateUrl: './dsr-report.component.html',
  styleUrls: ['./dsr-report.component.css']
})
export class DsrReportComponent implements OnInit {
    dsrData: any;
    loading: boolean = false;
    isDsrGeneated: Boolean = false;
    complimentry_bill: any;
    compl_netTotal = {total: 0, netAmount: 0};
    cancel_bill: any;
    can_netTotal = {total: 0, netAmount: 0};
    discount_bill: any;
    refunded_bill: any;
    paid_duplicate_bills: any;
    unpaid_duplicate_bills: any;
    edit_bills: any;
    suspicious_bills: any;
    dis_netTotal = {total: 0, netAmount: 0};
    refund_netTotal = {total: 0, netAmount: 0, refund_amt: 0 };
    paid_duplicate_netTotal = {total: 0, netAmount: 0};
    unpaid_duplicate_netTotal = {total: 0, netAmount: 0};
    edit_bill_netTotal = {total: 0, netAmount: 0};
    suspicious_bill_netTotal = {total: 0, netAmount: 0};
    report_detail: any;
    report_netTotal = {total: 0, netAmount: 0, discountAmount: 0, totalCashAmount: 0 , totalWallet: 0 };
    allPos: any = [];
    selectedPos: any;
    start_date: any;
    end_date: any;
    tax_details: any;
    tax_netTotal = {total: 0, cashTotal: 0};
    pos_name: any;
    pos: any;
    empData: any;
    mappedPos: any = [];
    cancelBillNumbers: any = '';
    discount_BillNumbers: any = '';
    refunded_BillNumbers: any = '';
    paid_Duplicat_BillNumbers: any = '';
    unpaid_Duplicat_BillNumbers: any = '';
    public hideRoundOff: boolean;
    ShopToken: any;
    isAmtReset: boolean = false;
    networkconnection:any;
    isConnected = true;
    onlineAvailsPos: any[] = ['ZOMATO', 'SWIGGY', 'UBER', 'BHOJ_ORDER'];
    posWisedDsr: any = [];
    constructor(
      private mainService: MainService,
      private snackBarService: SnackBarService,
      private spinner: NgxSpinnerService,
      private dialog: MatDialog
    ) {
      this.pos = JSON.parse(localStorage.getItem('pos'));
      this.mappedPos = JSON.parse(localStorage.getItem('mappedPos'));
      this.empData = JSON.parse(localStorage.getItem('employee'));
    }

ngOnInit() {

    const postData = {
        employeeId: this.empData.id,
        posIdList: [this.pos.id]
    };
    this.selectedPos = this.pos.id;
    this.getReportsData(postData);
    this.getAllPOS();
  }

  getReportsData(data) {
    this.mainService.getOnlineDailyReport(data).subscribe(res => {
      let result = res.response[this.selectedPos];
      this.dsrData = result;
    });
  }

  getAllPOS() {
      this.allPos.push(this.pos);
      this.selectedPos = this.pos.id;
      if (this.mappedPos && this.mappedPos.length > 0) {
        this.mappedPos.forEach(element => {
            this.allPos.push(element.posDetails);
        });
    }
  }


  changePOS(event) {
      const postData = {
        employeeId: this.empData.id,
        posIdList: [event.target.value]
      };
      this.getReportsData(postData);
  }


  openDialogBox() {
    const testDialogRef = this.dialog.open(DsrReportPopupComponent, {
      width: '850px',
      height: 'auto',
      data: {
        tableId: ''
      }
    });
    testDialogRef.afterClosed().subscribe(result => {
      if ( result && result.length > 0 ) {
        const rd = {
          employeeId : this.empData.id,
          posIdList: result
          };
        this.loading = true;
        this.mainService.generateonlinepposDSR(rd).subscribe(res => {
          if (res.status === 'success') {
            this.isDsrGeneated = true;
            result.forEach(element => {
              this.posWisedDsr.push(res.response[element]);
            });
            this.snackBarService.showSuccess('Daily Sale Report Generated Successfully...');
          } else {
            this.snackBarService.showError('Daily Sale Report Not Generated, Please try again...');
          }
          setTimeout( () => {
            this.loading = false;
          }, 500 );
        });
        setTimeout( () => {
          this.loading = false;
        }, 500 );
      } else {
        console.log('Empty array return');
      }

  });
}

donwload_dsr(dsr_id){
  this.loading = true;
  this.mainService.download_dsrpdf(dsr_id).subscribe(res => {
    this.loading = false;
    if(res){
      this.loading = false;
      this.snackBarService.showError(res);
    }
  });
}

}
