import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'filter'})
export class FilterPipe implements PipeTransform {
  transform(data, field: string, value: string) {
    if (value && value.toLowerCase() == 'all') {
      return data;
    }
    if (value) {
      // data = data.filter(t => t.billNo.toString().includes(value.toLowerCase()));
      return data;
    } else if (value == '' || value == undefined ) {
      return data;
    }
    const result = [];
    for (const val of data) {
        if (val[field] == value) {
            result.push(val);
        }
    }
    return result;
  }
}
