    getAddonTextForBill(item): string {
        let text = '';
        if (!item || !item.addons) {
            return '';
        }
        for (let i = 0; i < item.addons.length; i++) {
            if (i == 0) {
                text += '(Addons: with ';
            } else if (i == (item.addons.length - 1)) {
                text += ' and ';
            } else {
                text += ', ';
            }
            //text += item.addons[i].quantity + ' ' + item.addons[i].name;
            text += item.addons[i].name;
        }
        if(text!=''){
          text += ')';
        }
        return text;
    }

    getAddonTextForNewKot(item): string {
        let text = '';
        if (!item || !item.addons) {
            return '';
        }
        for (let i = 0; i < item.addons.length; i++) {
            if (i == 0) {
               text += '(Addons: with ';
            } else if (i == (item.addons.length - 1)) {
                text += ' and ';
            } else {
                text += ', ';
            }
            // text += item.quantity + ' ' + item.addons[i];
            text += item.addons[i];
        }
        if(text!=''){
          text += ')';
        }
        return text;
    }

    getAddonTextForPreviousKot(item) {
        let text = '';
        if (!item || !item.addons || item.addons.length === 0 || item.addons == 0) {
            return '';
        }
        const addons = item.addons.split(',');

        for (let i = 0; i < addons.length; i++) {
            if (i == 0) {
                text += '(Addons: with ';
            } else if (i == (addons.length - 1)) {
                text += ' and ';
            } else {
                text += ', ';
            }
            text += item.quantity + ' ' + this.addons.find(a => {
                return a.id == addons[i];
            }).name;
        }
        if(text!=''){
          text += ')';
        }
        return text;
    }

