    getQrCodeData(posId, type, no)  {
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.httpClient.get(env.phpUrl + 'qr-code.php?posId=' + posId + '&type=' + type + '&no=' + no , options)
        .map((data: any) => {
          return data;
        });

    }

    download_dsrpdf(dsr_id) {       
        return this.httpClient.get(env.phpUrl+'qr-code.php?dsr_id='+dsr_id+'&authKey='+localStorage.getItem('authKey')).map((res:any) =>{
         //   return res;
         var message = '';
          if (res.status == 'success') {
            window.location.href =  env.phpUrl+'qr-code.php?download=pdf';
            return message;
          } else {
            message = 'Something wrong try again';
            return message;
          }
        });
    }

