import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MainService } from '../../main.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { OrderService } from '../../orders/order.service';
import { SnackBarService } from '../../../../services/snackbar.service';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-mobile-order-bill-pay',
  templateUrl: './mobile-order-bill-pay.component.html',
  styleUrls: ['./mobile-order-bill-pay.component.css']
})
export class MobileOrderBillPayComponent implements OnInit, AfterViewInit {
  loading: boolean = false;
  table;
  billPreview;
  paymentForm: FormGroup;
  order;
  billId;
  parcelNo: any;
  disableBtn: boolean = false;
  constructor(
    public dialogRef: MatDialogRef<MobileOrderBillPayComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any,
    private mainService: MainService,
    private orderService: OrderService,
    private snackBarService: SnackBarService,
    private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.loading = true;
    this.parcelNo = this.data.order.parcelNo;
    this.billPreview = this.data.order.billBean;
    this.createPaymentForm();
  }

  ngAfterViewInit() {
    this.loading = false;
    this.paymentForm.controls.cashAmount.setValue(this.billPreview.netAmount);
    this.paymentForm.controls.cardAmount.setValue(this.billPreview.netAmount);
  }

  onClose(status) {
    this.dialogRef.close(status);
  }

  createPaymentForm() {
    this.paymentForm = new FormGroup({
      method: new FormControl('cash', Validators.required),
      cashAmount: new FormControl(''),
      cardAmount: new FormControl(''),
      cardReceipt: new FormControl('', [Validators.required]),
      smartCardAmount: new FormControl(''),
      smartCardNumber: new FormControl(''),
      smartCardReceipt: new FormControl(''),
      wallet: new FormControl(''),
      walletType: new FormControl(''),
      tipAmount: new FormControl('')
    });
  }

  setPaymentMethod(type) {
    this.paymentForm.controls.method.setValue(type);
    if(type === 'cash') {
      this.disableBtn = false;
    }
  }


  getBillAmount() {
    const payment = this.paymentForm.value;
    const amount = {
      isCash: false,
      isCredit: false,
      cashAmount: 0,
      creditAmount: 0,
      receiptNo: '',
      tipAmount: parseFloat(this.paymentForm.controls.tipAmount.value),
      offerAmount: 0,
      walletAmount: 0,
      checkAmount: 0,
      smartCardAmount: 0,
      checkNo: '',
      checkFor: '',
      walletId: '',
      smartCardReceipt: '',
      smartCardNumber: '',
      billId: ''
    };
    switch (payment.method) {
      case 'cash':
        if (isNaN(payment.cashAmount) || payment.cashAmount < this.getIntValue(this.billPreview.netAmount)) {
          this.snackBarService.showError('Cash amount can not be less than net amount');
          return;
        } else {
          amount.cashAmount = payment.cashAmount;
        }
        break;
      case 'card':
        if (isNaN(payment.cardAmount) || payment.cardAmount < this.getIntValue(this.billPreview.netAmount)) {
          this.snackBarService.showError('Card amount can not be less than net amount');
          return;
        }
        if (  !payment.cardReceipt.trim() ) {
          this.snackBarService.showError('Card receipt can not be empty');
          return;
        }
        console.log(payment.cardReceipt);
        amount.creditAmount = payment.cardAmount;
        amount.receiptNo = payment.cardReceipt;
        break;
      case 'smartCard':
        if (isNaN(payment.smartCardAmount) || payment.smartCardAmount < this.getIntValue(this.billPreview.netAmount)) {
          this.snackBarService.showError('Smart Card amount can not be less than net amount');
          return;
        } else if (payment.smartCardNumber == '') {
          this.snackBarService.showError('Smart card number can not be empty');
          return;
        } else if (payment.smartCardReceipt == '') {
          this.snackBarService.showError('Smart card receipt can not be empty');
          return;
        } else {
          amount.smartCardAmount = payment.smartCardAmount;
          amount.smartCardReceipt = payment.smartCardReceipt;
          amount.smartCardNumber = payment.smartCardNumber;
        }
        break;
      case 'wallet':
        if (isNaN(payment.walletAmount) || payment.walletAmount < this.getIntValue(this.billPreview.netAmount)) {
          this.snackBarService.showError('Wallet amount can not be less than net amount');
          return;
        } else if (payment.walletType == '') {
          this.snackBarService.showError('Wallet type can not be empty');
          return;
        } else {
          amount.walletAmount = payment.wallet;
          amount.walletId = payment.walletType;
        }
        break;
      default:
        this.snackBarService.showError('Payment method not suppoted by software');
        return;
    }
    return amount;
  }


  getIntValue(val) {
    return parseInt(val, 10);
  }

  billPay() {
    this.disableBtn = true;
    const amount = this.getBillAmount();
    amount.billId = this.billPreview.id;
    if (amount) {
      this.mainService.mobileOrderPayBill(amount).subscribe(res1 => {
        if (res1.status === 'success') {
          this.snackBarService.showSuccess('Bill Successfully paid');
          this.loading = false;
          this.onClose(true);
        } else {

          this.snackBarService.showError(res1.message ? res1.message : res1.response);
        }
         this.loading = false;
      });
    }

  }

  enableSubmitBtn() {
    this.disableBtn = false;
  }

}
