import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MainService } from '../../main.service';
import { SnackBarService } from '../../../../services/snackbar.service';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-zomato-order-cancel',
  templateUrl: './zomato-order-cancel.component.html',
  styleUrls: ['./zomato-order-cancel.component.css']
})
export class ZomatoOrderCancelComponent implements OnInit {

  cancelReasonId;
  description;
  order;
  isForceCancel = false;
  cancelationReasons: any;

  constructor(
    public dialogRef: MatDialogRef<ZomatoOrderCancelComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private mainService: MainService,
    private spinner: NgxSpinnerService,
    private snackbar: SnackBarService) { }

  ngOnInit() {
    this.order = this.data.order;
    this.cancelationReasons = this.order.cancelOrderMessageBeans;
  }

  onClose(status) {
    this.spinner.hide();
    this.dialogRef.close(status);
  }

  onCancel() {
    if (this.cancelationReasons && !this.cancelReasonId) {
      return this.snackbar.showError('Choose cancel reason id');
    }

    if (!this.description) {
      return this.snackbar.showError('Enter description');
    }
    this.spinner.show();
    const data = {
      orderId: this.order.id,
      desc: this.description,
      zomatoRejectionId: this.cancelReasonId,
      // force: this.isForceCancel
    };

    this.mainService.cancelOnlineOrder(data, this.order.posId).subscribe(res => {
      if (res.status === 'success') {
        this.onClose(true);
      } else {
        this.snackbar.showError(res.response);
        this.onClose(false);
      }
      this.spinner.hide();
    });

  }

}
