import { Component, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { MainService } from '../main.service';
import { timer } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { HelperService } from '../../../misc/helper.service';
import { ZomatoOrderCancelComponent } from '../../main/dialogs/zomato-order-cancel/zomato-order-cancel.component';
import { MatDialog } from '@angular/material';
import { SnackBarService } from '../../../services/snackbar.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-zomato',
  templateUrl: './zomato.component.html',
  styleUrls: ['./zomato.component.css']
})
export class ZomatoComponent implements OnInit {
  loading: boolean = false;
  isPlayAudio = false;
  mappedPos = [];
  zomatoPos;
  orders: any;
  filterByMobileNumber;
  orderTypes;
  visiableOptions = {
    pending: true,
    active: true,
    processed: true,
    outForDelivery: true,
    cancelled: true
  };
  CurrentOrderData: any;

  constructor(
    private mainService: MainService,
    private activatedRoute: ActivatedRoute,
    private helperService: HelperService,
    private dialog: MatDialog,
    private snackBar: SnackBarService,
    private datePipe: DatePipe,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {
  }

  ngOnInit() {
    this.loading = false;
    this.orders = this.activatedRoute.snapshot.data['orders'];

    this.mainService.activeLink = 'zomato';
    const timerSubscription = timer(0, 1000).subscribe(val => {
      this.isPlayAudio = !this.isPlayAudio;
    });

    timer(0, 10000).subscribe(val => {
      this.mainService.activeLink = 'zomato';
      if (this.router.url !== '/login') {
      this.reloadOrders();
      }
    });

    timer(0, 2000).subscribe(val => {
      if (this.orders.PENDING && this.orders.PENDING.length > 0) {
         this.mainService.playAudio();
      } else {
         this.mainService.pauseAudio();
      }
    });

    this.orderTypes = this.mainService.getOrderTypesKeyValue();
  }


  reloadOrders() {
    this.mainService.activeLink = 'zomato';
    const curr_posId = location.href.split('/')[6];
    if (this.mainService.zomatoPos && this.mainService.zomatoPos.id === curr_posId) {
      this.mainService.getZomatoOrder(this.mainService.zomatoPos.id)
      .subscribe(res => {
        this.orders = res;
      });
    } else {
        this.mappedPos = JSON.parse(localStorage.getItem('mappedPos'));
        this.zomatoPos = this.mappedPos.find(m => m.posDetails.posTypeId === '1a7e52fe-5122-11e9-9996-bc5ff4c06e58'
         && m.posDetails.id === curr_posId);
        this.mainService.getZomatoOrder(this.zomatoPos.posDetails.id)
        .subscribe(res => {
          this.orders = res;
        });
    }
    this.loading = false;
  }

  getOrderTypeAbbreviation(orderTypeId) {
    return this.mainService.getOrderTypeAbbreviation(orderTypeId);
  }

  timeStampToTime(timestamp) {
    return this.helperService.timeStampToTime(timestamp);
  }

  printZomatoKot(order) {
    this.mainService.printZomatoKot(order).subscribe((res: any) => {
      if (res.status == 'success') {
        this.snackBar.showSuccess('Kot printed');
      } else {
        this.snackBar.showError(res.message);
      }
    });
  }

  onAcceptOrder(order) {
    this.loading = true;
    this.mainService.acceptOnlineOrder(order.id)
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order Accepted');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        this.loading = false;
      });

  }

  onReadyOrder(order) {
    this.loading = true;
    this.mainService.readyOnlineOrder(order.id)
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order Marked Ready');
          this.reloadOrders();
        } else {
          this.loading = false;
          this.snackBar.showError(res.response);
        }
      });
  }
 
  onGenerateBillOrder(order) {
    this.loading = true;
    this.mainService.generateBillOnlineOrder({ orderId: order.id, description: '' })
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Bill generated');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        this.loading = false;
      });
  }

  onRejectOrder(order) {
    this.loading = true;
    const testDialogRef = this.dialog.open(ZomatoOrderCancelComponent, {
      width: '800px',
      height: 'auto',
      data: {
        order: order
      }
    });

    testDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.reloadOrders();
      }
    });
  }

  onOutForDelivery(o) {
    this.loading = true;
    this.mainService.outForDeliveryOnlineOrder({ orderId: o.id })
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order is out for delivery');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
          this.loading = false;
        }
      });
  }

  onCompleteOrder(o) {
    this.loading = true;
    this.mainService.completeOnlineOrder({ orderIdList: [o.id] })
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order Completed');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        this.loading = false;
      });
  }

  onlyPositiveNumberInput(event) {
    const val = event.target.value;
    if (isNaN(val) || val < 0) {
      this.snackBar.showAlert('Only number input is allowed');
     // event.target.value = "";
      this.filterByMobileNumber = '';
    }
  }

}
