import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { of } from 'rxjs';

@Injectable()
export class HelperService {

    TABLES_MAPPING = {
        ADDONS: 'addons',
        ITEMS: 'item',
        CATEGORY: 'item_category',
        SUBCATEGORY: 'item_sub_category',
        ITEM_REMARKS: 'item_remark',
        REST_CATEGORY: 'rest_category',
        TAX_MASTER: 'tax_master',
        HSN_MASTER: 'hsn_master',
        DINING_TBL: 'dining_table',
        EMP_TBL: 'emplyoee',
        POS_TBL: 'pos',
        RESTAURANT: 'restaurant',
        ACTION_PERMISSION: 'action_permission',
        CUSTOMER_TBL: 'customer',
        ITEMS_MASTER: 'item_master',

    };

    constructor() {

    }

    getCurrentDateTime() {
        const date = new Date();
        const day = date.getDate();
        const month = date.getMonth() + 1;
        const year = date.getFullYear();
        const hour = date.getHours();
        const minuits = date.getMinutes();
        const second = date.getSeconds();

        return { date: `${day}/${month}/${year}`, time: `${hour}:${minuits}:${second}` };
    }

    getCurrentDate() {
        return new Date().toISOString().slice(0, 19).replace('T', ' ');
    }

    
    convert24to12(time) {
        time = time.toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
        if (time.length > 1) {
            time = time.slice(1);
            time[5] = +time[0] < 12 ? 'AM' : 'PM';
            time[0] = +time[0] % 12 || 12;
        }
        return time.join('');
    }

    timeStampToTime(timestamp) {
        const date = new Date(timestamp * 1000);
        const hour = date.getHours();
        const minuits = date.getMinutes();
        const time = `${hour}:${minuits}:00`;
        return this.convert24to12(time);
    }

    fillArray(num) {
        return Array.from(new Array(num), (val, index) => index + 1);
    }

    isEqual(value, other): boolean {

        if (value == null && other == null) {
            return true;
        }

        if (value == undefined && other == undefined) {
            return true;
        }

        const type = Object.prototype.toString.call(value);
        if (type !== Object.prototype.toString.call(other)) {
            return false;
        }
        if (['[object Array]', '[object Object]'].indexOf(type) < 0) {
            return false;
        }
        const valueLen = type === '[object Array]' ? value.length : Object.keys(value).length;
        const otherLen = type === '[object Array]' ? other.length : Object.keys(other).length;
        if (valueLen !== otherLen) {
            return false;
        }
        const compare = function (item1, item2) {
            const itemType = Object.prototype.toString.call(item1);
            if (['[object Array]', '[object Object]'].indexOf(itemType) >= 0) {
                if (!this.isEqual(item1, item2)) {
                    return false;
                }
            } else {
                if (itemType !== Object.prototype.toString.call(item2)) {
                    return false;
                }
                if (itemType === '[object Function]') {
                    if (item1.toString() !== item2.toString()) {
                        return false;
                    }
                } else {
                    if (item1 !== item2) {
                        return false;
                    }
                }

            }
        };

        if (type === '[object Array]') {
            for (let i = 0; i < valueLen; i++) {
                if (compare(value[i], other[i]) === false) {
                    return false;
                }
            }
        } else {
            for (const key in value) {
                if (value.hasOwnProperty(key)) {
                    if (compare(value[key], other[key]) === false) {
                        return false;
                    }
                }
            }
        }
        return true;
    }

    dateDiff(d1, d2) {
        // const newDate = new Date(d2);
        // const offset = newDate.getTimezoneOffset();
        // newDate.setMinutes(newDate.getMinutes() - offset);
        // d2 = newDate;
        const ms = (new Date(d1).getTime()) - (new Date(d2).getTime()) ;
        let d, h, m, s;
        s = Math.floor(ms / 1000);
        m = Math.floor(s / 60);
        s = s % 60;
        h = Math.floor(m / 60);
        m = m % 60;
        d = Math.floor(h / 24);
        h = h % 24;

        return { days: d, hour: h, minuits: m, second: s };
    }

}
