import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { environment } from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { of } from 'rxjs';
import { MainService } from '../main.service';


@Injectable()
export class ParcelService {
    constructor(private httpClient: HttpClient,
    private mainService: MainService) {

    }

    getNextKotNo(posId): Observable<any> {
        return this.httpClient.get(environment.localApi + `/pos/getKot?pos_id=${posId}`)
            .map((res: any) => {
                return res.response.kotNo;
            });
    }

    getParcelOrder(parcelId): Observable<any> {
        return this.httpClient.get(environment.localApi + `/orders/by-id/${parcelId}`)
            .map((res: any) => {
                return res.response;
            });
    }
    
    getWebLocalParcel(): Observable<any> {
        let pos = localStorage.getItem('pos');
        if(!pos){ return null; }
        // get order type by id 29cceab9084811e6a93cc81f66433dc4 = pickup type && 01bd7374084811e6a93cc81f66433dc4 = home delivery type
        return this.httpClient.get(environment.localApi + `/orders/by-type/${this.mainService.pos.id}?type=29cceab9084811e6a93cc81f66433dc4,01bd7374084811e6a93cc81f66433dc4`)
            .map((res: any) => {
                return res.response;
            });
    }

    getShopToken(shopTokenId): Observable<any> {
        return this.httpClient.get(environment.localApi + `/orders/by-id/${shopTokenId}`)
            .map((res: any) => {
                return res.response;
            });
    }

    getWebLocalToken(): Observable<any> {
        return this.httpClient.get(environment.localApi + `/orders/by-type/${this.mainService.pos.id}?type=21446f78b91811e68956c81f66433dc4`)
            .map((res: any) => {
                return res.response;
            });
    }
    getWebLocalTable(): Observable<any> {
        // get order type by id 01bd7fc0084811e6a93cc81f66433dc4 = dining type
        return this.httpClient.get(environment.localApi + `/orders/by-type/${this.mainService.pos.id}?type=01bd7fc0084811e6a93cc81f66433dc4`)
            .map((res: any) => {
                return res.response;
            });
    }

    getWebLocalTableforJoinBill(): Observable<any> {
        // get order type by id 01bd7fc0084811e6a93cc81f66433dc4 = dining type
        return this.httpClient.get(environment.localApi + `/orders/by-type/${this.mainService.pos.id}?type=01bd7fc0084811e6a93cc81f66433dc4,29cceab9084811e6a93cc81f66433dc4`)
            .map((res: any) => {
                return res.response;
            });
    }

    orderOutForDelivery(id): Observable<any> {
        return this.httpClient.get(`${environment.localApi}/parcel/status/outForDelivery/${id}`)
        .map((response: any) => {
            return response.response;
        });
    }

    orderComplete(id): Observable<any> {
        return this.httpClient.get(`${environment.localApi}/parcel/status/complete/${id}`)
        .map((response: any) => {
            return response.response;
        });
    }
}
