import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MainService } from '../../main.service';
import { SnackBarService } from '../../../../services/snackbar.service';
import { NgxLoadingModule, ngxLoadingAnimationTypes } from 'ngx-loading';

@Component({
  selector: 'app-mobile-order-cancel-dialog',
  templateUrl: './mobile-order-cancel-dialog.component.html',
  styleUrls: ['./mobile-order-cancel-dialog.component.css']
})
export class MobileOrderCancelDialogComponent implements OnInit {
  public description: any;
  loading: boolean = false;
  order;
  isForceCancel = false;

  constructor(
    public dialogRef: MatDialogRef<MobileOrderCancelDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private mainService: MainService,
    private snackbar: SnackBarService) { }

  ngOnInit() {
    this.order = this.data.order;
  }

  onClose(status) {
    this.dialogRef.close(status);
  }

  onCancel() {
    this.loading = true;
    if ( this.description === undefined || !this.description.trim()) {
     this.snackbar.showError('Please enter description');
     this.loading = false;
     return;
    }
    const data = {
      orderId: this.order.id,
      desc: this.description,
      force: this.isForceCancel
    };

    this.mainService.cancelOnlineOrder(data, this.order.posId).subscribe(res => {
      this.loading = false;
      if (res.status === 'success') {
        this.onClose(true);
      } else {
        this.snackbar.showError(res.response);
        this.onClose(false);
      }
  });
  }

}
