import { Injectable } from '@angular/core';
import { MainService } from '.././components/main/main.service';

import { Resolve } from '@angular/router';

import { ActivatedRouteSnapshot } from '@angular/router';
import { OrderService } from '../components/main/orders/order.service';
import { ParcelService } from '../components/main/parcel/parcel.service';
import { of } from 'rxjs';

@Injectable()
export class PrinterResolver implements Resolve<any> {
    constructor(private mainService: MainService) { }

    resolve(route: ActivatedRouteSnapshot) {
        return this.mainService.getAllPrinters();
    }
}
