import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'OnlineOrderByZomatoCustomer'})
export class OnlineOrderByZomatoCustomerPipe implements PipeTransform {
  transform(data, field: string, value: string) {
    if (!value) {
      return data;
    }
    const result = [];
    if (data) {
      for (const val of data) {
        if (val.orderInfoBean.customer_details && val.orderInfoBean.customer_details[field].includes(value)) {
          result.push(val);
        }
      }
    }
    return result;
  }
}
