import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MainService } from '../main.service';
import { timer } from 'rxjs';
import { SnackBarService } from '../../../services/snackbar.service';
import { HelperService } from '../../../misc/helper.service';
import { MatDialog } from '@angular/material';
import { MobileOrderCancelDialogComponent } from '../dialogs/mobile-order-cancel-dialog/mobile-order-cancel-dialog.component';
import { MobileOrderBillPayComponent } from '../dialogs/mobile-order-bill-pay/mobile-order-bill-pay.component';
import { Router } from '@angular/router';
import { DatePipe } from '@angular/common';
import { NgxLoadingModule, ngxLoadingAnimationTypes } from 'ngx-loading';

@Component({
  selector: 'app-mobile-order',
  templateUrl: './mobile-order.component.html',
  styleUrls: ['./mobile-order.component.css']
})
export class MobileOrderComponent implements OnInit {
  orders;
  loading: boolean = false;
  currentOrderView;
  currentKotNo;
  filterByMobileNumber;
  visiableOptions = {
    pending: true,
    active: true,
    processed: true,
    outForDelivery: true,
    cancelled: true
  };
  orderTypes;
  CurrentOrderData: any;
  public pos: any;
  mappedPos: any;
  mobileOrdrerPos: any;
  curr_posId;

  constructor(
    private activatedRoute: ActivatedRoute,
    private mainService: MainService,
    private snackBar: SnackBarService,
    private helperService: HelperService,
    private dialog: MatDialog,
    private router: Router,
    private datePipe: DatePipe
    ) {
    this.mainService.activeLink = 'mobile';
  }

  ngOnInit() {
      this.loading = true;
      // setTimeout(()=> {
      //   this.loading = false;
      // }, 3000);
    
      this.pos = JSON.parse(localStorage.getItem('pos'));

      this.mappedPos = JSON.parse(localStorage.getItem('mappedPos'));
      setInterval(() => { 
        this.curr_posId = location.href.split('/')[6];
        this.mobileOrdrerPos = this.pos.posTypeId === '405a7604658711e996d7bc5ff4c06e58' ? { ...this.pos } : false;
        if (!this.mobileOrdrerPos) {
            this.mobileOrdrerPos = this.mappedPos.find(m => m.posTypeId === '405a7604658711e996d7bc5ff4c06e58' && m.id === this.curr_posId);
      }
        if (!this.currentOrderView || (this.currentOrderView && this.currentOrderView.posId != this.curr_posId)) {
        this.currentOrderView = '';
      }
    }, 2000);
  
      this.orders = this.activatedRoute.snapshot.data['orders'];
      console.log(this.orders);
      if (this.orders.PENDING && this.orders.PENDING.length > 0) {
      this.setCurrentOrdreView(this.orders.PENDING[0]);
    }

      this.reloadOrders();
      timer(15000, 10000).subscribe(val => {

      // let conn = localStorage.getItem('connectionDetector');
      if (this.router.url != '/login' ) {
        this.reloadOrders();
      }
    });

      this.orderTypes = this.mainService.getOrderTypesKeyValue();
    // =====sound=====

      timer(0, 2000).subscribe(val => {
      if (this.orders.PENDING && this.orders.PENDING.length > 0) {
       this.mainService.playAudio();
      } else {
       this.mainService.pauseAudio();
     }
    });
      const data =  this.mainService.bhojOrderObservable$.subscribe(res => {
        this.orders = res;
      });
      setTimeout( () => {  data.unsubscribe(); }, 400);

  }

  getFromPage(kotDetails, next, prev) {
    if (!kotDetails.page) {
      kotDetails.page = 1;
    }

    if (next) {
      kotDetails.page++;
    }

    if (prev) {
      kotDetails.page--;
    }

    return kotDetails.kotDetailsBeanList.slice((kotDetails.page - 1) * 5, (kotDetails.page) * 5);
  }

  isNextPage(kotDetails) {
    return kotDetails.kotDetailsBeanList.length / 5 > kotDetails.page;
  }

  getAddonTextForBill(item): string {
    return this.mainService.getAddonTextForBill(item);
  }

  setCurrentOrdreView(o) {
    this.currentOrderView = { ...o };
    this.currentKotNo = this.currentOrderView.kotBeanList[0].kotNo;
  }

  getKotItem(currentKotNo) {
    return this.currentOrderView.kotBeanList.find(k => k.kotNo == currentKotNo).kotDetailsBeanList;
  }

  getKotRemark(currentKotNo) {
    return this.currentOrderView.kotBeanList.find(k => k.kotNo == currentKotNo).kotRemark;
  }

  getTaxDetails(currentKotNo) {
    return this.currentOrderView.kotBeanList.find(k => k.kotNo == currentKotNo).kotRemark;
  }

  reloadOrders() {
    let mobileOrdrerPosId = location.href.split('/')[6];
    if (!mobileOrdrerPosId) {  this.loading = false; return; }
    this.mainService.getMobileOrder(mobileOrdrerPosId)
      .subscribe(res => {
        this.loading = false;
        this.orders = res;
        // if (this.orders) {
        // }
      });
    }

  getOrderTypeAbbreviation(orderTypeId) {
    return this.mainService.getOrderTypeAbbreviation(orderTypeId);
  }

  timeStampToTime(timestamp) {
    return this.helperService.timeStampToTime(timestamp);
  }

  onAcceptOrder(order) {

    this.loading = true;
    let d = new Date() ;
    let Currdate = this.datePipe.transform( d, 'yyyy-MM-dd HH:mm:ss');
    let newDate = this.helperService.getCurrentDate();
    if (newDate === undefined) {
        newDate = Currdate;
      };

    const data = {
      id: order.id ? order.id : null,
      date: newDate,
      // kotOrderType: this.orderTypes[o.orderTypeName],
      kotOrderType: order.orderTypeId,
      orderTypeId: order.orderTypeId ? order.orderTypeId : this.orderTypes[order.orderTypeName],
      customerId: order.customerId ? order.customerId : '',
      tableList: order.tableList,
      kotDetailsBeanList: order.kotBeanList[0].kotDetailsBeanList,
      pax: order.pax ,
      posId: order.posId,
      orderRemark: order.orderRemark,
      kotRemark: order.kotRemark ? order.kotRemark : '',
      statusId: order.statusId,
      is_mobile_order: 1
    };

    this.mainService.acceptOnlineOrder(order.id)
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order Accepted');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        setTimeout(() => {
          this.loading = false;
        }, 5000);
    });
  }

  onReadyOrder(order) {
    this.loading = true;
    const data = {
      id: order.id ? order.id : null,
      date: this.helperService.getCurrentDate(),
      // kotOrderType: this.orderTypes[o.orderTypeName],
      kotOrderType: order.orderTypeId,
      orderTypeId: order.orderTypeId ? order.orderTypeId : this.orderTypes[order.orderTypeName],
      customerId: order.customerId ? order.customerId : '',
      tableList: order.tableList,
      kotDetailsBeanList: order.kotBeanList[0].kotDetailsBeanList,
      pax: order.pax ,
      posId: order.posId,
      orderRemark: order.orderRemark,
      kotRemark: order.kotRemark ? order.kotRemark : '',
      statusId: order.statusId,
      is_mobile_order: 1
    };
    this.mainService.readyOnlineOrder(order.id)
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order Marked Ready');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        setTimeout(() => {
          this.loading = false;
        }, 5000);
    });
  }

  onGenerateBillOrder(order) {
    this.loading = true;
    const data = {
      id: order.id ? order.id : null,
      date: this.helperService.getCurrentDate(),
      // kotOrderType: this.orderTypes[o.orderTypeName],
      kotOrderType: order.orderTypeId,
      orderTypeId: order.orderTypeId ? order.orderTypeId : this.orderTypes[order.orderTypeName],
      customerId: order.customerId ? order.customerId : '',
      tableList: order.tableList,
      kotDetailsBeanList: order.kotBeanList[0].kotDetailsBeanList,
      pax: order.pax ,
      posId: order.posId,
      orderRemark: order.orderRemark,
      kotRemark: order.kotRemark ? order.kotRemark : '',
      statusId: order.statusId,
      is_mobile_order: 1
    };

    this.mainService.generateBillOnlineOrder({ orderId: order.id, description: '' })
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Bill generated');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        setTimeout(() => {
          this.loading = false;
        }, 5000);
    });
  }

  onRejectOrder(order) {
    const data = {
      id: order.id ? order.id : null,
      date: this.helperService.getCurrentDate(),
      // kotOrderType: this.orderTypes[o.orderTypeName],
      kotOrderType: order.orderTypeId,
      orderTypeId: order.orderTypeId ? order.orderTypeId : this.orderTypes[order.orderTypeName],
      customerId: order.customerId ? order.customerId : '',
      tableList: order.tableList,
      kotDetailsBeanList: order.kotBeanList[0].kotDetailsBeanList,
      pax: order.pax ,
      posId: order.posId,
      orderRemark: order.orderRemark,
      kotRemark: order.kotRemark ? order.kotRemark : '',
      statusId: order.statusId,
      is_mobile_order: 1
    };

    
    const testDialogRef = this.dialog.open(MobileOrderCancelDialogComponent, {
      width: '800px',
      height: 'auto',
      data: {
        order: order
      }
    })
    testDialogRef.afterClosed().subscribe(result => {
      this.reloadOrders();
      if (result) {
        this.currentOrderView = null;
        this.reloadOrders();
        this.snackBar.showSuccess('Order Cancel');
      }
      setTimeout(() => {
        this.loading = false;
      }, 5000);
    });
    
  }

  onOutForDelivery(o) {
    this.loading = true;
    const data = {
      id: o.id ? o.id : null,
      date: this.helperService.getCurrentDate(),
      // kotOrderType: this.orderTypes[o.orderTypeName],
      kotOrderType: o.orderTypeId,
      orderTypeId: o.orderTypeId ? o.orderTypeId : this.orderTypes[o.orderTypeName],
      customerId: o.customerId ? o.customerId : '',
      tableList: o.tableList,
      kotDetailsBeanList: o.kotBeanList[0].kotDetailsBeanList,
      pax: o.pax ,
      posId: o.posId,
      orderRemark: o.orderRemark,
      kotRemark: o.kotRemark ? o.kotRemark : '',
      statusId: o.statusId,
      is_mobile_order: 1
    };

    this.mainService.outForDeliveryOnlineOrder({ orderId: o.id })
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order is out for delivery');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        setTimeout(() => {
          this.loading = false;
        }, 5000);
    });
  }

  onCompleteOrder(o) {
    this.loading = true;
    const data = {
      id: o.id ? o.id : null,
      date: this.helperService.getCurrentDate(),
      // kotOrderType: this.orderTypes[o.orderTypeName],
      kotOrderType: o.orderTypeId,
      orderTypeId: o.orderTypeId ? o.orderTypeId : this.orderTypes[o.orderTypeName],
      customerId: o.customerId ? o.customerId : '',
      tableList: o.tableList,
      kotDetailsBeanList: o.kotBeanList[0].kotDetailsBeanList,
      pax: o.pax ,
      posId: o.posId,
      orderRemark: o.orderRemark,
      kotRemark: o.kotRemark ? o.kotRemark : '',
      statusId: o.statusId,
      is_mobile_order: 1,
      kush: 1
    };
    this.mainService.completeOnlineOrder({ orderIdList: [o.id] })
      .subscribe(res => {
        if (res.status === 'success') {
          this.snackBar.showSuccess('Order Completed');
          this.reloadOrders();
        } else {
          this.snackBar.showError(res.response);
        }
        setTimeout(() => {
          this.loading = false;
        }, 5000);
    });
  }

  onBillPay(o = '') {
    // this.loading = true;
    if (!o) {
      o = this.currentOrderView;
    }
    const testDialogRef = this.dialog.open(MobileOrderBillPayComponent, {
      width: '800px',
      height: 'auto',
      data: {
        order: o
      }
    });
    testDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.reloadOrders();
        this.currentOrderView = null;
      }
    });
  }

  onlyPositiveNumberInput(event) {
    const val = event.target.value;
    if (isNaN(val) || val < 0) {
      this.snackBar.showAlert('Only number input is allowed');
     // event.target.value = "";
      this.filterByMobileNumber = '';
    }
  }

  detectCheckbox(event) {
     
    if (event.checked == false && (this.visiableOptions.active == false  || this.visiableOptions.pending == false || this.visiableOptions.processed == false || this.visiableOptions.outForDelivery == false )) {
      this.currentOrderView = null
    }
  }
}
