import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'itemFilter' })
export class ItemFilterPipe implements PipeTransform {
    transform(data, subCategoryId: string, categoryId: string) {
        if (categoryId && categoryId.toLowerCase() == 'all') {
            return data;
        }

        if (subCategoryId) {
            return data.filter(d => d.itemSubCategoryId == subCategoryId);
        }

        if (categoryId) {
            return data.filter(d => d.itemCategoryId == categoryId);
        }
    }
}
