import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'OnlineOrderByCustomer'})
export class OnlineOrderByCustomerPipe implements PipeTransform {
  transform(data, field: string, value: string) {
    if (!value) {
      return data;
    }
    const result = [];
    for (const val of data) {

        if (val.customerBean && val.customerBean[field].includes(value)) {
            result.push(val);
        }

    }
    return result;
  }
}
