import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MainService } from '../../main.service';
import { SnackBarService } from '../../../../services/snackbar.service';

@Component({
  selector: 'app-bill-kot-details-dialog',
  templateUrl: './bill-kot-details-dialog.component.html',
  styleUrls: ['./bill-kot-details-dialog.component.css']
})
export class BillKotDetailsDialogComponent implements OnInit {
  loading= true;
  kotData =[];
  kotNo;
  orderId;

  constructor(
    public dialogRef: MatDialogRef<BillKotDetailsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
     private mainService: MainService,
     private snackBar: SnackBarService
  ) {  }

  ngOnInit() {

    //---get data from one component to anather component-----
    this.kotNo = this.data.kotNo;
    this.orderId = this.data.orderId;

    this.mainService.kotTrackBykotId(this.kotNo, this.orderId).subscribe(res => {
    this.kotData = res.kots;
    this.loading = false;
    });
    
  }

  
  onClose(status) {
    this.dialogRef.close(status);
  }



}
